<template>
  <div>
    <!-- 设置返回按钮 START-->
    <header class="my-work-header">
      <a><img src="../../../static/images/per_fh.png" alt="返回按钮" @click="back"></a>
      <span>我的作品</span>
      <a class="about">与我相关</a>
    </header>
    <!-- 设置返回按钮 END -->
    <section>
      <div class="creation_item" v-for="(workInfo, index) in myWorkList" :key="index">
        <div class="creation_item_header">
          <div>
            <a><img v-lazy="workInfo.userAvatar" alt="用户头像"></a>
          </div>
          <div>
            <h5><a>{{workInfo.nickName}}</a></h5>
            <p>{{workInfo.dateAdded | dateDiff}}</p>
          </div>
          <div>
            <img v-if="workInfo.workStatus == 'NOTREVIEWED'" class="pass_img" :src="passing" alt="审核图片">
            <img v-else-if="workInfo.workStatus == 'NOTPASS'" class="pass_img" :src="noPass" alt="审核图片">
          </div>
        </div>
        <div class="creation_item_content" @click="enterPerCenter(workInfo)">
          <p class="work_content">{{workInfo.content}}</p>
          <div class="creation_item_row">
            <img v-for="(workImg, index) in workInfo.contentPics.split(',')" :src="workImg" alt="作品图片" :key="index">
          </div>
          <div v-if="workInfo.contentPics && workInfo.contentPics.split(',').length > 6" class="workWask">{{workInfo.contentPics.split(',').length}}+</div>
        </div>
        <div class="delete_btn">
          <div>阅读 {{workInfo.browseCount}}</div><span v-if="workInfo.workStatus == 'NOTPASS'" style="color: #3fb140" @click="enterEditWork(workInfo)">编辑</span> <a @click="deleteWorkItem(workInfo,index)">删除</a></div>
        <div class="creation_item_footer">
          <div>
            <a @click="activePraise(workInfo)"><img :src="workInfo.isPraise == 'Y' ? yesPraiseImg : noPraiseImg" alt="点赞"><span>{{workInfo.numberFabulous}}</span></a>
          </div>
          <div>
            <a @click="enterPerCenter(workInfo)"><img :src="commentImg" alt="评论"><span>{{workInfo.numberComments}}</span></a>
          </div>
          <div>
            <a><img :src="shareImg" alt="分享"><span>{{workInfo.numberShare}}</span></a>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
export default {
  data() {
    return {
      page: 1,
      myWorkList: new Array(),
      noPraiseImg:
        require('../../assets/img/jef4eada.png'),
      yesPraiseImg:
        require('../../assets/img/jef4f7vr.png'),
      commentImg:
        require('../../assets/img/jef4f7vr.png'),
      shareImg:
        require('../../assets/img/jehyyijh.png'),
      baseUrl: "http://192.168.124.4:8081",
      noPass:
        require('../../assets/img/jei9l6qk.png'),
      passing:
        require('../../assets/img/jei9l6qk.png'),
      falgNoData: false,
      page: 1,
      pagination: true,
      showData: false
    }
  },
  created() {
    window.onscroll = () => {
      this.dropDown()
    }
    if (!this.$store.state.user) {
      this.$router.push("/mine")
    }
    this.showLoading = 1
    this.axios
      .get("/api/myWorkCircle", {
        params: {
          page: this.page
        }
      })
      .then(data => {
        this.myWorkList = data.data.result.list
        this.myWorkList.forEach(element => {
          if (element.userAvatar.indexOf("http") == -1) {
            element.userAvatar = this.baseUrl + element.userAvatar
          }
        })
        this.showLoading = 0
      })
      .catch(err => {
        console.log(err)
      })
  },
  methods: {
    // 分页
    dropDown() {
      //真实内容的高度
      var pageHeight = Math.max(
        document.body.scrollHeight,
        document.body.offsetHeight
      )
      //视窗的高度
      var viewportHeight =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight ||
        0
      //隐藏的高度
      var scrollHeight =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop ||
        0
      if (this.falgNoData) {
        //数据全部加载完了
        
        this.$Indicator.close()
        
        this.$Toast({
          message: '没有更多作品了',
          position: 'middle',
          duration: 1000
        })
        return
      } else if (
        pageHeight - viewportHeight - scrollHeight == 0 &&
        this.pagination
      ) {
        //如果满足触发条件，执行
        this.$Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        })
        
        this.page += 1
        this.axios
          .get("/api/myWorkCircle", {
            params: {
              page: this.page
            }
          })
          .then(res => {
            if (this.pagination) {
              if (res.data.result.list.length > 0) {
                this.worksList = this.worksList.concat(res.data.result.list)
                this.$Indicator.close()
              } else {
                this.$Indicator.close()
                this.$Toast({
                  message: '没有更多作品了',
                  position: 'middle',
                  duration: 1000
                })
                this.pagination = false
                this.showData = true
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
      }
    },
    // 点赞
    activePraise(workInfo) {
      console.log(workInfo)
      this.axios
        .get("/api/likeWorks", {
          params: {
            worksId: workInfo.worksId,
            action: workInfo.isPraise == "Y" ? "NORMAL" : "UPVOTE"
          }
        })
        .then(data => {
          if (
            data.data.status === "success" &&
            data.data.result == "取消点赞 -1！"
          ) {
            workInfo.isPraise = "N"
            workInfo.numberFabulous = --workInfo.numberFabulous
            this.$Toast({
              message: '取消点赞成功',
              position: 'middle',
              duration: 500
            })
          } else if (
            data.data.status === "success" &&
            data.data.result == "点赞 +1！"
          ) {
            workInfo.isPraise = "Y"
            workInfo.numberFabulous = ++workInfo.numberFabulous
            this.$Toast({
              message: '点赞成功',
              position: 'middle',
              duration: 500
            })
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 进入编辑作品
    enterEditWork(workInfo) {
      console.log(workInfo)
      this.$router.push({
        name: "publishWorkCircle",
        params: {
          id: workInfo.worksId
        }
      })
    },
    // 进入作品详情
    enterPerCenter(info) {
      this.axios
        .get("/api/plusWorks", {
          params: {
            worksId: info.worksId,
            type: "LL"
          }
        })
        .then(data => {
          console.log(data)
          this.$router.push({
            name: "workCircleCommentList",
            params: {
              id: info.worksId
            }
          })
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 返回上一步
    back() {
      this.$router.back(-1)
    },
    // 删除作业
    deleteWorkItem(workInfo, index) {
      this.axios
        .get("/api/user/deleteWork", {
          params: {
            workId: workInfo.worksId
          }
        })
        .then(data => {
          if (data.data.result == "success") {
            this.$Toast({
              message: '删除作品成功',
              position: 'middle',
              duration: 500
            })
            this.myWorkList.splice(index, 1)
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>



<style lang="less" scoped>
/* 顶部 返回 按钮 START */
.my-work-header {
  position: sticky;
  top: 0;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  background-color: #fff;
  z-index: 99;
  & > span {
    line-height: 3.125rem;
    text-align: center;
  }
  & > a,
  & > span {
    display: block;
    -webkit-box-flex: 1;
    flex: 1;
    height: 2.5rem;
  }
  & > a:nth-child(1) {
    padding: 0.625rem 0 0 0.625rem;
    & > img {
      width: 1.25rem;
      height: 1.875rem;
    }
  }
}
.about {
  padding: 0.9375rem 0.625rem 0 0;
  color: rgb(17, 160, 76);
  text-align: right;
}
/* 顶部 返回 按钮 END */
.work_content {
  padding: 0.625rem 0;
}
/* 作品圈 列表 item 展示 START */
.creation_item {
  background-color: #fff;
  margin-top: 0.625rem;
}
.creation_item_header {
  display: -webkit-flex;
  display: flex;
  height: 3.75rem;
  overflow: hidden;
}
.creation_item_header > div:nth-child(1) {
  -webkit-box-flex: 2;
  flex: 2;
  padding: 0.625rem 0 0 0.9375rem;
  text-align: center;
}
.creation_item_header > div:nth-child(1) > a > img {
  width: 3.125rem;
  height: 3.125rem;
}
.creation_item_header > div:nth-child(2) {
  -webkit-box-flex: 6;
  flex: 6;
  padding: 0.9375rem 0 0 0;
}
.creation_item_header > div:nth-child(2) > p {
  color: #aaa;
}
.creation_item_header > div:nth-child(3) {
  -webkit-box-flex: 2;
  flex: 2;
}
.pass_img {
  width: 4.375rem;
  margin-top: -4px;
}
/* 作品内容 START */
.creation_item_content {
  padding-left: 1.25rem;
  position: relative;
  & > .workWask {
    position: absolute;
    bottom: 0;
    right: 0.625rem;
    height: 1.5625rem;
    width: 3.125rem;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 1;
    color: #fff;
    text-align: center;
    line-height: 1.5625rem;
    border-radius: 1.25rem;
  }
}
.creation_item_row {
  width: 100%;
  img {
    width: 33%;
    height: 6.25rem;
  }
}
/* 作品内容  END */
/* 删除按钮 START */
.delete_btn {
  width: 1005;
  text-align: right;
  padding: 0.3125rem 1.875rem 0.3125rem 1.25rem;
}
.delete_btn > div {
  float: left;
}
.delete_btn > a {
  color: #aaa;
}
/* 删除按钮 END */
/* 点赞/评论/转发 START */
.creation_item_footer {
  display: -webkit-flex;
  display: flex;
  padding: 0.625rem 0 0.625rem 0;
}
.creation_item_footer > div {
  -webkit-box-flex: 1;
  flex: 1;
  text-align: center;
}
.creation_item_footer > div > a > img {
  width: 1.25rem;
  margin-right: 0.25rem;
  vertical-align: middle;
}
.creation_item_footer > div > a > span {
  vertical-align: middle;
  color: #666;
}
/* 店长弄/评论/转发 END */
/* 作品圈 列表 item 展示 END */
</style>



